import axios from "axios";
import authHeader from "./auth.header";
import { API_URL } from "./api.url";
let url = API_URL;
export async function addEducation(userId, institution, startDate, endDate) {
    return await axios.post(url + "/education/" + userId, { institution, startDate, endDate }, { headers: authHeader() });
}
export async function deleteEducation(id) {
    return axios.delete(url + "/education/" + id, { headers: authHeader() });
}
export async function addExperience(userId, institution, startDate, endDate) {
    return await axios.post(url + "/experience/" + userId, { institution, startDate, endDate }, { headers: authHeader() });
}
export async function deleteExperience(id) {
    return axios.delete(url + "/experience/" + id, { headers: authHeader() });
}
