import axios from "axios";
import authHeader from "./auth.header";
import { API_URL } from "./api.url";
let url = API_URL + "/profiles/";
export async function getProfiles(page, type, loc, price, rating) {
    return await axios.get(url +
        "filtered?price=" +
        price +
        "&rating=" +
        rating +
        "&page=" +
        page +
        "&type=" +
        type +
        "&loc=" +
        loc);
}
export async function createEmptyProfile() {
    return await axios.post(url + "empty", {}, { headers: authHeader() });
}
export async function getProfileById(id) {
    return await axios.get(url + id);
}
export async function deleteProfileAndUser(id) {
    return await axios.delete(url + id, { headers: authHeader() });
}
export async function updateProfile(id, firstName, lastName, profilePicture, type, description, price, phoneNumber, sector, address) {
    return await axios.patch(url + id, {
        firstName,
        lastName,
        profilePicture,
        type,
        description,
        price,
        phoneNumber,
        sector,
        address,
    }, { headers: authHeader() });
}
